<template>
  <div :id="this.uuid">
  </div>
</template>

<script>
let uuid = 0

export default {
  name: 'VideoPlayer',
  props: {
    id: {
      required: true
    },
    partialWatchedEventOn: {
      type: Number,
      default: 0.4
    },
    steps: {
      type: Number,
      default: 20
    }
  },
  emits: ['statisticsUpdate'],
  data () {
    return {
      player: null,
      duration: null,
      completionArray: Array(this.steps).fill(false),
      startStep: null,
      statistics: {
        id: this.id,
        partialWatched: false,
        fullyWatched: false,
        doneWatching: false
      }
    }
  },
  computed: {
    completion () {
      const total = this.completionArray.reduce((previous, current) => previous + current)
      return total / this.completionArray.length
    }
  },
  watch: {
    completion (newCompletion, oldCompletion) {
      if (newCompletion >= this.partialWatchedEventOn && oldCompletion < this.partialWatchedEventOn) {
        this.statistics.partialWatched = true
        this.$emit('statisticsUpdate', this.statistics)
      }
      if (newCompletion === 1) {
        this.statistics.fullyWatched = true
        this.$emit('statisticsUpdate', this.statistics)
      }
    }
  },
  methods: {
    getStep (time) {
      const stepSize = this.duration / this.steps
      const quantisedTime = time - (time % stepSize)
      return Math.floor(quantisedTime / stepSize)
    }
  },
  mounted () {
    this.player = new bluebillywig.Player(`https://demo.bbvms.com/p/default/c/${this.id}.json`, {
      target: document.getElementById(this.uuid),
      autoPlay: 'false'
    })
    this.player.on('loadedmetadata', (event) => {
      this.duration = event.target.dataset.duration
    })
    this.player.on('playing seeked', (event) => {
      this.startStep = this.getStep(event.target.dataset.currenttime)
    })
    this.player.on('pause seeking', (event) => {
      this.completionArray.fill(true, this.startStep, this.getStep(event.target.dataset.currenttime) + 1)
    })
    this.player.on('ended', () => {
      this.statistics.doneWatching = true
      this.$emit('statisticsUpdate', this.statistics)
    })
  },
  beforeUnmount () {
    if (this.startStep != null) {
      this.completionArray.fill(true, this.startStep, this.getStep(this.player.getCurrentTime()) + 1)
      if (!this.statistics.partialWatched && this.completion >= this.partialWatchedEventOn) {
        this.statistics.partialWatched = true
        this.$emit('statisticsUpdate', this.statistics)
      }
      if (!this.statistics.fullyWatched && this.completion === 1) {
        this.statistics.fullyWatched = true
        this.$emit('statisticsUpdate', this.statistics)
      }
    }
    this.player.off('loadedmetadata playing seeked pause seeking ended')
  },
  beforeCreate () {
    this.uuid = 'player-' + uuid.toString()
    uuid += 1
  }
}
</script>
