<template>
  <div class="post">
    <div v-if="error" class="error">{{ error }}</div>

    <div v-if="post" class="content">
      <h2>{{ post.title }}</h2>
      <p>{{ post.body }}</p>

      <VideoPlayer v-if="mappings[post.id]" :id="mappings[post.id]" @statistics-update="updateUserStatistics"/>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import VideoPlayer from '@/components/VideoPlayer.vue'

export default {
  name: 'Post',
  components: {
    VideoPlayer
  },
  methods: {
    updateUserStatistics: _.debounce(function (statistics) {
      if (this.$route.query.user) {
        this.postData(`https://jsonplaceholder.typicode.com/users/${this.$route.query.user}`, {
          videoStatistics: [
            { ...statistics }
          ]
        })
      }
    }, 1000),
    async postData (url, data) {
      try {
        const response = await fetch(url, {
          method: 'PATCH',
          body: JSON.stringify(data),
          headers: {
            'Content-type': 'application/json; charset=UTF-8'
          }
        })
        if (response.status >= 200 && response.status <= 299) {
          const user = await response.json()
          console.log(user)
        } else {
          throw Error(response)
        }
      } catch (error) {
        console.log(error)
      }
    }
  },
  async beforeRouteEnter (to, from, next) {
    try {
      const response = await fetch(`https://jsonplaceholder.typicode.com/posts/${to.params.id}`)
      if (response.status >= 200 && response.status <= 299) {
        const post = await response.json()
        next(vm => { vm.post = post })
      } else {
        throw Error(response.status)
      }
    } catch (error) {
      next(vm => { vm.error = error.toString() })
    }
  },
  async beforeRouteUpdate (to, from) {
    this.post = null
    this.error = null
    try {
      const response = await fetch(`https://jsonplaceholder.typicode.com/posts/${to.params.id}`)
      if (response.status >= 200 && response.status <= 299) {
        this.post = await response.json()
      } else {
        throw Error(response.status)
      }
    } catch (error) {
      this.error = error.toString()
    }
